.input {
    flex-grow: 8;
    padding: 10px;
    /*width: 100%;*/
    height: 40px;
    background: #F5F7FB;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    overflow: hidden;
    resize: none;
    font-size: 14px;
    line-height: 17px;
}

.input:focus {
    border: 1px solid dodgerblue;
}

.errorInput {
    border: 1px solid red;
    outline: red;
}

.button {
    flex-grow: 1;
    margin-left: 44px;
